body {
  background-color: #3c3b3b;
}

* {
  color: white;
}

.no-border-button {
  border: none;
}

.question-button {
  font-size: 20px;
  padding: 5px;
  margin-right: 5px;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.auth {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.logout-button {
  padding: 10px 5px;
}

.auth span {
  margin: 0 5px;
}

.page-header {
  margin: 0 5px;
  border: none;
}

.list-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.list {
  display: flex;
  flex-direction: column;
  min-width: 30%;
  text-align: center;
}

.list-title {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}

.list-add-button {
  margin-left: 50px;
  margin-right: 5px;
  margin-top: 15px;
}

.card {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: left;
  background-color: #5a5d5e;
  padding: 10px;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  margin: 10px 20px;
}

@keyframes fade {
  0% {
    bottom: 0;
    opacity: 1;
  }
  100% {
    bottom: -1em;
    opacity: 0;
  }
}
.slider {
  overflow: hidden;
  -webkit-transition-property: all;
  -webkit-transition-duration: 0.5s;
  -webkit-transition-timing-function: ease-in-out;
  -moz-transition-property: all;
  -moz-transition-duration: 0.5s;
  -moz-transition-timing-function: ease-in-out;
}
.slider-closed {
  max-height: 0;
}

.slider-opened {
  max-height: 500px;
}

.card-title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  h3 {
    margin-top: 0;
  }

  .card-button {
    background-color: rgba(0, 0, 0, 0);
    border: none;
  }
}

.title-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.card-details {
  p {
    padding: 5px;
  }

  ul {
    list-style: none;
  }

}

.checklist-task .container{
  overflow: hidden;
  display: inline-block;
  position: relative;
  margin-bottom: 12px;
  margin-right: 5px;
  width: 250px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  input{
    position: absolute;
    opacity: 0;
    width: 0;
    cursor: pointer;

    &:checked ~ .checkmark {
      background-color: #2196F3;
      &:after {
        display: block;
      }

      &:hover {
        background-color: #66b7f3;
      }
    }
  }

  .check-button {
    background-color: #2196F3;
    padding: 2px 5px;
    margin-right: 5px;

    &:hover {
        background-color: #66b7f3;
      }
  }

  .unchecked {
    background-color: #ccc;

      &:hover {
        background-color: #dedede;
      }

      span {
        color: rgba(0, 0, 0, 0);
      }
  }
}

.checklist-task {
  display: flex;

  &:first-child {
    margin-top: 10px;
    padding-top: 10px;
  }
}

.checklist-add-task {
  border: 2px dashed #bbb;
  color: black;
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  border-radius: 3px;
}

.loader-wrapper {
  display: flex;

  div {
    display: flex;
    margin: auto;
  }
}

.global-loader {
  position: absolute;
  top: calc(50vh - 100px);
  left: calc(50vw - 100px);
}

.checklist-task-remove {
  padding: 2px 5px;
  align-self: center;
}

.checklist {
  ul {
    padding: 0;
  }
}

.modal-content {
  background-color: #5a5d5e;

  .create-card {
    display: flex;
    flex-direction: column;
    align-content: center;
    padding-left: 50px;

    label {
      display: flex;
      flex-direction: column;
      margin: auto;
    }

    button {
      display: flex;
      width: 200px;
      margin: 20px auto;
    }

    input {
      border: 2px dashed #bbb;
      color: black;
      width: 100%;
      padding: 10px;
      margin-top: 5px;
      border-radius: 3px;
    }
  }
}